            WestPomeranian: "West Pomeranian Voivodeship or West Pomerania Province (in Polish, województwo zachodniopomorskie [vɔjɛˈvut͡stfɔ zaˈxɔdɲɔ pɔˈmɔrskʲɛ]; is a voivodeship (province) in northwestern Poland. It borders on Pomeranian Voivodeship to the east, Greater Poland Voivodeship to the southeast, Lubusz Voivodeship to the south, the German federal-states of Mecklenburg-West Pomerania and Brandenburg to the west, and the Baltic Sea to the north.",
